import simpleAddress from '@/components/simple-address/simple-address.nvue';
import { addPotentialCustomer } from '../../api/api.js'
export default {
	data() {
		return {
			"address": "",
			"city": "",
			"contact": "",
			"district": "",
			"intro": "",
			"mobile": "",
			"name": "",
			"position": "",
			"province": "",
		    pickerCity:"",
			cityPickerValueDefault: [0, 0, 1],
		};
	},
	components: {
		simpleAddress
	},
	methods:{
		toSaveInfo(){
			let params = {
				"address": this.address,
				"city":this.pickerCity.labelArr[1],
				"contact":this.contact,
				"district":this.pickerCity.labelArr[2],
				"intro":this.intro,
				"mobile":this.mobile,
				"name":this.name,
				"position": this.position,
				"province":this.pickerCity.labelArr[0],
			}
			addPotentialCustomer(params).then( res => {
				uni.showToast({
					title:res.message
				})
				setTimeout(() => {
					uni.navigateBack()
				},1000)
				
			})
		},
		openAddres() {
			this.cityPickerValueDefault = [0,0,1]
			this.$refs.simpleAddress.open();
		},
		onConfirmAddress(e){
			
			this.pickerCity = e;
			console.log('pickerCity=',this.pickerCity)
		}
	}
}
